/* src/components/Navbar/Navbar.css */
.navbar {
background-color: #333;
color: white;
padding: 1rem;
}

.navbar-container {
display: flex;
justify-content: space-between;
align-items: center;
}

.navbar-logo {
font-size: 1.5rem;
}

.navbar-menu {
list-style: none;
display: flex;
gap: 1rem;
}

.navbar-item a {
color: white;
text-decoration: none;
}

.navbar-item a:hover {
text-decoration: underline;
}